.wrapper_heading {
    margin: 2em 0 1em 0;
    padding: 1em;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    h2 {
        text-align: center;
        font-size: 40px; }

    p {
        text-align: center;
        width: 50%; } }

.row {
    display: grid;
    grid-template-columns: auto auto auto;
    grid-template-rows: auto;
    grid-column-gap: 20px;
    justify-items: center;
    align-items: center;
    @media screen and (max-width: 920px) {
        grid-template-columns: auto; }

    .card {
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        margin: 1em 0;
        height: auto;
        width: 314px;
        border-radius: 50px;
        background: #ffffff;

        .card-img-top {
            width: 314px;
            height: 200px;
            border-radius: 20px 20px 0 0; }
        .card-body {
            width: 314px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            h3 {

                font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-bold) var(--unnamed-font-size-35)/45px var(--unnamed-font-family-cormorant-garamond);
                color: var(--unnamed-color-161616);
                text-align: center;
                font: normal normal bold 35px/45px Cormorant Garamond;
                letter-spacing: -0.35px;
                color: #161616; }
            h6 {
                font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-medium) var(--unnamed-font-size-18)/var(--unnamed-line-spacing-22) var(--unnamed-font-family-montserrat);
                letter-spacing: var(--unnamed-character-spacing-0-9);
                color: var(--unnamed-color-898989);
                text-align: center;
                font: normal normal medium 18px/22px Montserrat;
                letter-spacing: 0.9px;
                color: #898989;
                text-transform: uppercase; }

            p {
                font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-normal) 16px/29px var(--unnamed-font-family-montserrat);
                color: var(--unnamed-color-898989);
                text-align: center;
                font: normal normal normal 14px/29px Montserrat;
                letter-spacing: 0.24px; } }

        .card-button {
            button {
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 0 0.8em;
                margin: 0 auto;
                width: 314px;
                height: 62px;
                outline: none;
                border: 0;
                cursor: pointer;
                border-radius: 0 0 20px 20px;
                // background: #53BFD2
                text-transform: uppercase;
                font-weight: bold;
                color: #fff;
                p {
                    margin: 0;
                    text-transform: lowercase;
                    display: flex;
                    align-items: center;
                    transition: .2s ease-in-out;
                    color: #161616;
                    font: italic normal 300 15px sans-sarif; }

                img {
                    position: absolute;
                    left: 50%;
                    width: 40px; }
                span {
                    margin-left: 0.3em;

                    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-normal) var(--unnamed-font-size-20)/var(--unnamed-line-spacing-34) var(--unnamed-font-family-montserrat);
                    color: var(--unnamed-color-161616);
                    text-align: right;
                    font: normal normal normal 20px/34px Montserrat;
                    letter-spacing: 0.5px;
                    color: #161616; }
                // &:hover
 } } } }                //     background: red
.showmore {
    display: flex;
    justify-content: center;
    margin: 1em 0 3em 0;
    padding: 0 1em;

    .showmore_btn {
        outline: none;
        border: none;
        padding: 0 1em;
        text-transform: uppercase;
        font-weight: bold;
        img {
            margin: 0 0 0 1em; } } }

.readmore {
    margin: 0 0.5em;
    text-decoration: underline;
    transition: .2s ease-in-out;
    &:hover {
        color: #53BFD2; } }

.no_match {
    text-align: center; }
